import React, { useEffect, useState } from 'react'
import './App.css'

import { trebleNotes } from './constants/notes'


const App = () => {

  const [noteIndex, setNoteIndex] = useState(0)

  const [possibleAnswers, setPossibleAnswers] = useState(['E', 'F', 'G', 'A', 'B', 'C', 'D'])


  const changeNote = () => {
    const r = Math.random() * trebleNotes.length | 0

    setNoteIndex(r)
    shuffleArray(possibleAnswers)
  }


  const shuffleArray = (arr) => {
    for (let i = 0; i < arr.length; i++) {
      const r = Math.random() * arr.length | 0

      const temp = arr[i]
      arr[i] = arr[r]
      arr[r] = temp
    }
  }


  const [isCorrect, setIsCorrect] = useState(false)


  const mapToComponent = () => {
    return React.createElement(trebleNotes[noteIndex].component)
  }

  const checkAnswer = (answer) => {
    const correctAnswer = trebleNotes[noteIndex].note.toUpperCase()

    if (answer === correctAnswer) {
      setIsCorrect(true)
      changeNote()
    } else {
      setIsCorrect(false)
    }

  }



  return (
    <section className="main-container">
      <h1>Choose the correct note for the treble clef</h1>
      <div className="question-container">
        {
          mapToComponent()
        }
      </div>

      <div className="options-container">
        {
          possibleAnswers.map(option => (
            <div key={option} className={`option ${isCorrect ? 'correct' : 'wrong'}`}
              onClick={checkAnswer.bind(this, option)}>
              {option}
            </div>
          ))
        }
      </div>
    </section>
  )
}

export default App